import React, { Component, KeyboardEvent } from 'react';
import { hot } from 'react-hot-loader';
import { merge } from 'rxjs';
import { debounceTime } from 'rxjs/operators';

import './App.css';

import { InjectionContext } from 'lib/react/context';
import { Inject, Provide } from 'lib/react/decorators';

import { Footer } from './Footer';
import { RouterService } from './services/router';
import { StateService } from './services/state';
import { TodoService } from './services/todo';
import { TodoItem } from './TodoItem';

@Provide([TodoService, StateService, RouterService])
class TodoMVC extends Component {
  static contextType = InjectionContext;

  @Inject(StateService) private stateService!: StateService;
  @Inject(TodoService) private todoService!: TodoService;

  inputRef: HTMLInputElement | null = null;

  componentDidMount(): void {
    merge(this.todoService.updated$, this.stateService.updated$)
      .pipe(debounceTime(16))
      .subscribe(() => this.forceUpdate());
  }

  private handleKeydown(e: KeyboardEvent): void {
    if (e.keyCode !== 13) {
      return;
    }

    e.preventDefault();

    const val = this.inputRef?.value;

    console.log(val);

    if (val) {
      this.todoService.addTodo(val);
      this.inputRef!.value = '';
    }
  }

  render() {
    const todoItems = this.todoService.shownTodos.map((todo) => {
      return <TodoItem key={todo.id} todo={todo}></TodoItem>;
    });

    const todoPart = this.todoService.todoCount ? (
      <section>
        <input
          type="checkbox"
          id="toggle-all"
          className="toggle-all"
          onChange={(e) => this.todoService.toggleAll(e.target.checked)}
          checked={this.todoService.activeTodoCount === 0}
        />
        <label htmlFor="toggle-all">Mark all as completed</label>
        <ul className="todo-list">{todoItems}</ul>
      </section>
    ) : null;
    const footerPart = this.todoService.todoCount ? <Footer></Footer> : null;

    return (
      <div>
        <header className="header">
          <h1>todos</h1>
          <input
            type="text"
            ref={(r) => (this.inputRef = r)}
            className="new-todo"
            placeholder="What needs to be done?"
            onKeyDown={(e) => this.handleKeydown(e)}
            autoFocus={true}
          />
        </header>
        {todoPart}
        {footerPart}
      </div>
    );
  }
}

export const App = hot(module)(() => <TodoMVC />);
