import { Ctor, Identifier, InitPromise } from './typing';

const singletonDependencies: [Identifier<any>, InitPromise<any>][] = [];

export function registerSingleton<T>(id: Identifier<T>, ctor: Ctor<T>): void {
  const index = singletonDependencies.findIndex(
    (d) => d.toString() === id.toString()
  );

  if (index !== -1) {
    singletonDependencies[index] = [id, new InitPromise<T>(ctor)];
    console.warn(`[WeDI] Duplicated registration of ${id.toString()}.`);
  } else {
    singletonDependencies.push([id, new InitPromise<T>(ctor)]);
  }
}

/**
 * For top-layer injectors to fetch all singleton dependencies.
 */
export function getSingletonDependencies(): [
  Identifier<any>,
  InitPromise<any>
][] {
  return singletonDependencies;
}
