import React, { Component, createContext } from 'react';

import { DependencyCollection } from 'lib/collection';
import { Injector } from 'lib/injector';
import { getSingletonDependencies } from 'lib/singleton';
import { Identifier } from 'lib/typing';

export interface InjectionContextProps {
  injector: Injector | null;
}

export const InjectionContext = createContext<InjectionContextProps>({
  injector: null
});
InjectionContext.displayName = 'context';

export const InjectionConsumer = InjectionContext.Consumer;
export const InjectionProvider = InjectionContext.Provider;

export interface InjectionProviderProps {
  providers?: [Identifier<any>, any][];
  collection?: DependencyCollection;
}

/**
 * The React binding of WeDI use the React context API to specify
 * injection positions and layered injector three.
 *
 * ```ts
 * <InjectorLayer providers={}>
 *   { children }
 * </InjectorLayer>
 * ```
 */
export class InjectionLayer extends Component<InjectionProviderProps> {
  renderChild(context: InjectionContextProps) {
    const { providers, collection, children } = this.props;
    const parentInjector = context.injector;
    const newDependencies = providers || [];

    // NOTE: In the root injector we should gather singleton dependencies.
    // TODO: this do not works for function component.
    if (!context.injector) {
      newDependencies.push(...getSingletonDependencies());
    }

    if (collection && providers) {
      console.warn(
        '[WeDI] you provide both "providers" and "collections", "providers" will be ignored.'
      );
    }

    let _collection: DependencyCollection;
    if (collection) {
      newDependencies.forEach((d) => collection.add(d[0], d[1]));
      _collection = collection;
    } else {
      _collection = new DependencyCollection(newDependencies);
    }

    const injector = parentInjector
      ? parentInjector.createChild(_collection)
      : new Injector(_collection);

    return (
      <InjectionProvider value={{ injector }}>{children}</InjectionProvider>
    );
  }

  render() {
    return (
      <InjectionConsumer>
        {(context) => this.renderChild(context)}
      </InjectionConsumer>
    );
  }
}
